<template>
  <div class="bill">
    <van-nav-bar title="交易记录" left-arrow @click-left="$router.back()" />
    <bill-filter-bar
      @coinChangeRecapture="coinChange"
      @typeChangeRecapture="typeChange"
      @timeChangeRecapture="timeChange"
    />
    <bill-list :bills="bills" @cancel="refreshBills" />
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, watch } from "vue"
import { NavBar, Tab, Tabs, Toast } from "vant"
import BillFilterBar from "./BillFilterBar.vue"
import BillList from "./BillList.vue"
import BillService, { Bill } from "@/services/bill"

export default defineComponent({
  name: "bill",
  components: {
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    BillFilterBar,
    BillList,
  },
  setup() {
    let bills = ref<Bill[]>([])
    let coin = ref("")
    let type = ref("")
    let time = ref("1")

    function refreshBills() {
      const loadingToast = Toast({ type: "loading", forbidClick: true, loadingType: "spinner" })
      // 请求数据
      BillService.getBills({ type: type.value, currency: coin.value, timeSort: time.value })
        .then(({ status, message, data }) => {
          if (status === 200) {
            bills.value = data
          } else {
            Toast(message)
          }
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    function coinChange(value: string) {
      coin.value = value === "全部币种" ? "" : coin.value
    }

    function typeChange(value: string) {
      type.value = value === "全部类型" ? "" : type.value
    }

    function timeChange(value: string) {
      // 1 倒序  arrow-up  arrow-down
      time.value = value === "arrow-up" ? "0" : "1"
    }

    watch([type, coin, time], refreshBills)

    onMounted(refreshBills)

    return { bills, refreshBills, coinChange, typeChange, timeChange }
  },
})
</script>
