import client, { ClientResponse } from "@/services/client"

interface UploadForm {
  file: File
}

interface ImageBlob {
  imgUrl?: string
}

export default class BlobService {
  public static upload(form: UploadForm): Promise<ClientResponse<ImageBlob>> {
    const data = new FormData()
    data.append("file", form.file)
    return client.post("/users/upload.php", data).then(({ data }) => data)
  }
}
