<template>
  <div class="bill-list-item">
    <van-cell-group>
      <van-cell title="类型" :value="type" />
      <van-cell value-class="bill-list-item__blue" title="数量" :value="coinCount" />
      <van-cell title="状态">
        <template #default>
          <template v-if="status !== '审核中'">{{ status }}</template>
          <template v-else>
            <template v-if="!loading && showCancel">
              {{ status }}
              <div class="bill-list-item__link" @click="onCancelWithdraw">
                <span>撤销</span>
              </div>
            </template>
            <van-loading v-if="loading" size="14px">撤销中...</van-loading>
          </template>
        </template>
      </van-cell>
      <van-cell title="创建时间" :value="createTime" />
      <van-cell title="单号" :value="order_no" />
      <van-cell title="备注" :value="comment" />
    </van-cell-group>

    <!-- 分割线 -->
    <van-divider class="bill-list-item__divider" />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from "vue"
import { Cell, CellGroup, Divider, Loading, Toast } from "vant"
import TransactionService from "@/services/transaction"

export default defineComponent({
  name: "BillListItem",
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Divider.name]: Divider,
    [Loading.name]: Loading,
  },
  props: {
    type: String,
    amount: String,
    createTime: String,
    order_no: String,
    comment: String,
    status: String,
    currency: String,
  },
  setup(props, { emit }) {
    // 后台定义好接口之后需要根据 type 增加 + 或者 -
    const coinCount = computed(() => props.amount + " " + props.currency?.toUpperCase())

    const loading = ref(false)
    const showCancel = ref(true)

    function onCancelWithdraw() {
      if (loading.value) {
        return
      }
      loading.value = true
      TransactionService.cancelWithdraw(props.order_no)
        .then(({ status, message }) => {
          if (status !== 200) {
            Toast(message)
            return
          }
          showCancel.value = false
          emit("cancel")
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      coinCount,
      loading,
      showCancel,
      onCancelWithdraw,
    }
  },
})
</script>

