<template>
  <div class="asset-withdrawal">
    <van-nav-bar title="转出" :border="false" left-arrow @click-left="$router.back()" />
    <asset-transaction-switch title="转出" v-model="state.type" />
    <van-form class="asset-withdrawal__form">
      <div class="asset-withdrawal__field-label">
        <div class="asset-withdrawal__field-label__title">转出数量</div>
        <div class="asset-withdrawal__field-label__value">矿工费: {{ fee }}</div>
      </div>
      <van-field
        class="asset-withdrawal__field"
        v-model="state.num"
        type="number"
        :formatter="numFormatter"
        :placeholder="`可转出金额 ${balance} ${typeUpper}`"
      />
      <div class="asset-withdrawal__field-label">转账地址</div>
      <van-field
        class="asset-withdrawal__field"
        v-model="state.address"
        placeholder="请输入钱包地址"
      />
      <div class="asset-withdrawal__field-label">交易密码</div>
      <van-field
        class="asset-withdrawal__field"
        v-model="state.paypassword"
        type="password"
        placeholder="请输入交易密码"
      />
      <div class="asset-withdrawal__field-label">短信验证</div>
      <van-field class="asset-withdrawal__field" v-model="state.code" placeholder="请输入验证码">
        <template #button>
          <captcha-link
            :sent="captchaSent"
            :time="60 * 1000"
            @send="sendCaptcha"
            @finish="captchaSent = false"
          >
            获得验证码
          </captcha-link>
        </template>
      </van-field>
      <div class="asset-withdrawal__tips">
        <div class="asset-withdrawal__tip">
          转账前请务必确认地址及币种信息无误，已转出，不可撤销。
        </div>
        <div class="asset-withdrawal__tip">
          <p class="asset-withdrawal__tip__title">提示：</p>
          <p class="asset-withdrawal__tip__line">1.最小转出数量为 0 {{ typeUpper }}</p>
          <p class="asset-withdrawal__tip__line">
            2.请务必保证提笔钱包地址的准确性，否则资产将无法找回；
          </p>
          <p class="asset-withdrawal__tip__line">3.矿工费具体多少由市场决定，平台不收取手续费；</p>
          <p class="asset-withdrawal__tip__line">
            4.提币审核会在24小时内完成，具体到账时间会受到网络影响，可能有所延迟；
          </p>
          <p class="asset-withdrawal__tip__line">5.如长时间未到账，请及时联系客服。</p>
        </div>
      </div>
      <div class="asset-withdrawal__submit">
        <van-button block type="primary" :loading="loading" @click="onSubmit()">
          确认转出
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, reactive, ref, watchEffect } from "vue"
import { Button, Field, Form, NavBar, Toast } from "vant"
import CaptchaLink from "@/components/CaptchaLink.vue"
import AssetTransactionSwitch from "@/views/asset/AssetTransactionSwitch.vue"
import TransactionService, { Withdraw } from "@/services/transaction"
import _ from "lodash"
import UserService from "@/services/user"
import TokenContext from "@/services/token"
import { useRouter } from "vue-router"
import WalletService, { Fee, Wallet } from "@/services/wallet"
import PaymentService, { PaymentList } from "@/services/payment"

export default defineComponent({
  name: "AssetWithdrawal",
  components: {
    AssetTransactionSwitch,
    CaptchaLink,
    [NavBar.name]: NavBar,
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button,
  },
  props: {
    type: {
      type: String,
      default: "btc",
    },
  },
  setup(props) {
    const { push } = useRouter()
    const captchaSent = ref(false)
    const loading = ref<boolean>(false)
    const wallet = ref<Wallet>()
    const feeObject = ref<Fee>()
    const paymentMethod = ref<PaymentList>()

    const state = reactive<Withdraw>({
      type: "btc",
      code: "",
      num: "",
      address: "",
      paypassword: "",
    })

    watchEffect(() => {
      state.type = props.type
    })

    const typeUpper = computed(() => _.toUpper(state.type))

    const balance = computed(() => {
      if (state.type === "btc") {
        return _.toNumber(wallet.value?.btc?.balance)
      } else if (state.type === "eth") {
        return _.toNumber(wallet.value?.eth?.balance)
      } else if (state.type === "usdt") {
        return _.toNumber(wallet.value?.usdt?.balance)
      }
      return 0
    })

    const fee = computed(() => {
      if (state.type === "btc") {
        return feeObject.value?.btc
      } else if (state.type === "eth") {
        return feeObject.value?.eth
      } else if (state.type === "usdt") {
        return feeObject.value?.usdt
      }
      return "0"
    })

    const paymentMethodAddress = computed(() => {
      if (state.type === "btc") {
        return paymentMethod.value?.btc
      } else if (state.type === "eth") {
        return paymentMethod.value?.eth
      } else if (state.type === "usdt") {
        return paymentMethod.value?.usdt
      }
      return ""
    })

    function refreshWithdrawal() {
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      Promise.all([
        WalletService.getWallet(),
        WalletService.getFee(),
        PaymentService.getPaymentMethod(),
      ])
        .then(([walletResponse, feeResponse, paymentMethodResponse]) => {
          const { status: walletStatus, message: walletMessage, data: walletData } = walletResponse
          const { status: feeStatus, message: feeMessage, data: feeData } = feeResponse
          const {
            status: paymentMethodStatus,
            message: paymentMethodMessage,
            data: paymentMethodData,
          } = paymentMethodResponse
          if (walletStatus !== 200) {
            Toast(walletMessage)
            return
          }
          if (feeStatus !== 200) {
            Toast(feeMessage)
            return
          }
          if (paymentMethodStatus !== 200) {
            Toast(paymentMethodMessage)
            return
          }
          feeObject.value = feeData
          wallet.value = walletData
          paymentMethod.value = paymentMethodData
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    onMounted(refreshWithdrawal)

    watchEffect(() => {
      state.address = paymentMethodAddress.value ?? ""
    })

    function onSubmit() {
      if (state.num <= 0) {
        Toast("转入数量不能为空")
        return
      }
      if (_.isEmpty(state.address)) {
        Toast("转账地址不能为空")
        return
      }
      if (_.isEmpty(state.paypassword)) {
        Toast("交易密码不能为空")
        return
      }
      if (_.isEmpty(state.code)) {
        Toast("验证码不能为空")
        return
      }
      loading.value = true
      TransactionService.createWithdraw(state)
        .then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            Toast("具体到账金额以实际为准，矿工费具体多少由市场决定，平台不收取手续费。")
            push("/assets/detail")
          }
        })
        .finally(() => {
          loading.value = false
        })
    }

    function sendCaptcha() {
      const loadingToast = Toast({
        type: "loading",
        message: "发送中...",
        forbidClick: true,
        loadingType: "spinner",
      })

      const { mobile } = TokenContext.getToken()

      UserService.sendCaptcha({ mobile, type: "withdraw" })
        .then(({ status, message }) => {
          if (status === 200) {
            captchaSent.value = true
          }
          Toast(message)
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    function numFormatter(value: string) {
      if (value === "") {
        return value
      }
      if (_.endsWith(value, ".")) {
        return value
      }
      const valueNumber = Number(value)
      return valueNumber < 0 ? 0 : valueNumber
    }

    return {
      state,
      loading,
      balance,
      fee,
      typeUpper,
      numFormatter,
      captchaSent,
      sendCaptcha,
      onSubmit,
    }
  },
})
</script>
