import client, { ClientResponse } from "@/services/client"

export interface Bill {
  id?: string
  uid?: string
  order_no?: string
  status?: string
  type?: string
  fee?: string
  create_time?: string
  updatetime?: string
  quantity?: string
  amound?: string
  direction?: string
  payId?: string
  imgUrl?: string
  comment?: string
  currency?: string
}

interface BillQuery {
  type?: string
  currency?: string
  timeSort?: string
}

export default class BillService {
  public static getBills(query: BillQuery): Promise<ClientResponse<Bill[]>> {
    return client
      .get(`/product/myWalletLog.php`, {
        params: query,
      })
      .then(({ data }) => data)
  }
}
