import client, { ClientResponse } from "@/services/client"

interface PaymentMethodValue {
  type?: string | undefined
  code?: string
  address?: string
}

export interface PaymentList {
  id?: string
  uid?: string
  btc?: string
  eth?: string
  usdt?: string
  chuangjiantime?: string
}

export default class PaymentService {
  public static addPaymentMethod(form: PaymentMethodValue): Promise<ClientResponse<void>> {
    return client.post("/money/addressAdd.php", form).then(({ data }) => data)
  }

  public static getPaymentMethod(): Promise<ClientResponse<PaymentList>> {
    return client.post("/money/addressList.php").then(({ data }) => data)
  }
}
