
import { defineComponent, onMounted, ref, watchEffect } from "vue"
import { Tab, Tabs, Toast } from "vant"
import AssetDetailTab from "@/views/asset/AssetDetailTab.vue"
import WalletService, { Wallet } from "@/services/wallet"

export default defineComponent({
  name: "AssetDetailTabs",
  components: {
    AssetDetailTab,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
  },
  setup(props, { emit }) {
    const active = ref("btc")
    const eye = ref(true)
    const wallet = ref<Wallet>({})

    watchEffect(() => {
      emit("changeType", active.value)
    })

    function refreshWallet() {
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      WalletService.getWallet()
        .then(({ data }) => {
          wallet.value = data
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    onMounted(refreshWallet)

    function onEye(e: boolean) {
      eye.value = e
    }

    return {
      active,
      eye,
      onEye,
      wallet,
    }
  },
})
