<template>
  <div class="asset-topup">
    <van-nav-bar
      title="转入"
      :border="false"
      right-text="联系客服"
      left-arrow
      @click-left="$router.back()"
      @click-right="customerServicePopupShow = true"
    />
    <div class="asset-topup__content">
      <asset-topup-header />
      <van-form class="asset-topup__form">
        <div class="asset-topup__field-label">转入数量</div>
        <van-field
          class="asset-topup__field"
          v-model="state.num"
          type="number"
          :formatter="numFormatter"
          placeholder="请输入转入数量"
        />
        <div class="asset-topup__field-label">区块链交易ID</div>
        <van-field
          class="asset-topup__field"
          v-model="state.payId"
          placeholder="请输入区块链交易ID"
        />
        <div class="asset-topup__field-label">交易凭证</div>
        <div class="asset-topup__uploader">
          <van-uploader v-model="imageUrls" :after-read="afterRead" :max-count="1" />
        </div>
        <div class="asset-topup__submit">
          <van-button
            block
            type="primary"
            :disabled="disabled"
            :loading="loading"
            @click="onSubmit"
          >
            确认转入
          </van-button>
        </div>
      </van-form>
    </div>
    <div class="asset-topup__card">
      <van-image
        class="asset-topup__qrcode"
        height="100"
        width="100"
        :src="require('./topup-qrcode.png')"
      ></van-image>
      <div class="asset-topup__account">{{ address }}</div>
      <div class="asset-topup__actions">
        <div class="asset-topup__link" @click.stop="copyAddress()">复制地址</div>
        <div class="asset-topup__link" @click.stop="downloadQrcode()">保存图片</div>
        <a
          ref="downloadQrcodeRef"
          download="topup-qrcode.png"
          style="display: none"
          :href="require(`./topup-qrcode.png`)"
        ></a>
      </div>
      <div class="asset-topup__tips">
        <p>温馨提示：</p>
        <p>1.转入金额以实际到账数量为准，请自行计算扣除手续费。</p>
        <p>2.本地址只接受USDT-TRC20转入，切勿支付其他币种。</p>
      </div>
    </div>
    <customer-service-popup v-model:show="customerServicePopupShow"></customer-service-popup>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from "vue"
import { Button, Field, Form, Image, NavBar, Toast, Uploader } from "vant"
import AssetTopupHeader from "@/views/asset/AssetTopupHeader.vue"
import BlobService from "@/services/blob"
import _ from "lodash"
import TransactionService, { TopupForm } from "@/services/transaction"
import { useRouter } from "vue-router"
import CustomerServicePopup from "@/components/CustomerServicePopup.vue"
import copyToClipboard from "copy-to-clipboard"

interface VanFile {
  url: string
  file: File
  status: string
  message: string
}

export default defineComponent({
  name: "AssetTopup",
  components: {
    CustomerServicePopup,
    AssetTopupHeader,
    [NavBar.name]: NavBar,
    [Image.name]: Image,
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button,
    [Uploader.name]: Uploader,
  },
  setup() {
    const { push } = useRouter()
    const address = ref("TK47ZFt1Rn2uUPkUn8JvQevxh1KJv2T1A6")
    const downloadQrcodeRef = ref<HTMLAnchorElement>()
    const state = reactive<TopupForm>({
      num: "",
      payId: "",
      imgUrl: "",
    })
    const imageUrls = ref<VanFile[]>([])
    const disabled = ref<boolean>(false)
    const loading = ref<boolean>(false)
    const customerServicePopupShow = ref<boolean>(false)

    function onSubmit() {
      if (state.num <= 0) {
        Toast("转入数量不能为空")
        return
      }
      if (_.isEmpty(state.payId)) {
        Toast("区块链交易ID不能为空")
        return
      }
      if (_.isEmpty(state.imgUrl)) {
        Toast("交易凭证不能为空")
        return
      }
      loading.value = true
      TransactionService.createTopup(state)
        .then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            push("/assets/detail")
          }
        })
        .finally(() => {
          loading.value = false
        })
    }

    function afterRead(file: VanFile) {
      file.status = "uploading"
      file.message = "上传中..."
      disabled.value = true
      BlobService.upload({ file: file.file })
        .then(({ status, message, data }) => {
          if (status !== 200) {
            Toast(message)
            file.status = "failed"
            file.message = "上传失败"
            return
          }
          file.status = ""
          if (data.imgUrl) {
            state.imgUrl = data.imgUrl
          }
        })
        .finally(() => {
          disabled.value = false
        })
    }

    function numFormatter(value: string) {
      if (value === "") {
        return value
      }
      if (_.endsWith(value, ".")) {
        return value
      }
      const valueNumber = Number(value)
      return valueNumber < 0 ? 0 : valueNumber
    }

    function downloadQrcode() {
      const a = downloadQrcodeRef.value
      if (a) {
        a.click()
      }
    }

    function copyAddress() {
      copyToClipboard(address.value)
      Toast("复制成功")
    }

    return {
      state,
      disabled,
      loading,
      address,
      downloadQrcodeRef,
      downloadQrcode,
      copyAddress,
      customerServicePopupShow,
      numFormatter,
      imageUrls,
      afterRead,
      onSubmit,
    }
  },
})
</script>
