import client, { ClientResponse } from "@/services/client"

export interface TopupForm {
  num: number | string
  payId: string
  imgUrl: string
}

export interface Withdraw {
  type: string
  code: string
  paypassword: string
  address: string
  num: number | string
}

export default class TransactionService {
  public static createTopup(form: TopupForm): Promise<ClientResponse<void>> {
    return client.post("/money/recharge.php", form).then(({ data }) => data)
  }

  public static createWithdraw(form: Withdraw): Promise<ClientResponse<void>> {
    return client.post("/money/withdraw.php", form).then(({ data }) => data)
  }

  public static cancelWithdraw(orderNo?: string): Promise<ClientResponse<void>> {
    return client.post("/money/cancel.php", { orderNo }).then(({ data }) => data)
  }
}
