
import { defineComponent, reactive, ref } from "vue"
import { Button, Field, Form, Image, NavBar, Toast, Uploader } from "vant"
import AssetTopupHeader from "@/views/asset/AssetTopupHeader.vue"
import BlobService from "@/services/blob"
import _ from "lodash"
import TransactionService, { TopupForm } from "@/services/transaction"
import { useRouter } from "vue-router"
import CustomerServicePopup from "@/components/CustomerServicePopup.vue"
import copyToClipboard from "copy-to-clipboard"

interface VanFile {
  url: string
  file: File
  status: string
  message: string
}

export default defineComponent({
  name: "AssetTopup",
  components: {
    CustomerServicePopup,
    AssetTopupHeader,
    [NavBar.name]: NavBar,
    [Image.name]: Image,
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button,
    [Uploader.name]: Uploader,
  },
  setup() {
    const { push } = useRouter()
    const address = ref("TK47ZFt1Rn2uUPkUn8JvQevxh1KJv2T1A6")
    const downloadQrcodeRef = ref<HTMLAnchorElement>()
    const state = reactive<TopupForm>({
      num: "",
      payId: "",
      imgUrl: "",
    })
    const imageUrls = ref<VanFile[]>([])
    const disabled = ref<boolean>(false)
    const loading = ref<boolean>(false)
    const customerServicePopupShow = ref<boolean>(false)

    function onSubmit() {
      if (state.num <= 0) {
        Toast("转入数量不能为空")
        return
      }
      if (_.isEmpty(state.payId)) {
        Toast("区块链交易ID不能为空")
        return
      }
      if (_.isEmpty(state.imgUrl)) {
        Toast("交易凭证不能为空")
        return
      }
      loading.value = true
      TransactionService.createTopup(state)
        .then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            push("/assets/detail")
          }
        })
        .finally(() => {
          loading.value = false
        })
    }

    function afterRead(file: VanFile) {
      file.status = "uploading"
      file.message = "上传中..."
      disabled.value = true
      BlobService.upload({ file: file.file })
        .then(({ status, message, data }) => {
          if (status !== 200) {
            Toast(message)
            file.status = "failed"
            file.message = "上传失败"
            return
          }
          file.status = ""
          if (data.imgUrl) {
            state.imgUrl = data.imgUrl
          }
        })
        .finally(() => {
          disabled.value = false
        })
    }

    function numFormatter(value: string) {
      if (value === "") {
        return value
      }
      if (_.endsWith(value, ".")) {
        return value
      }
      const valueNumber = Number(value)
      return valueNumber < 0 ? 0 : valueNumber
    }

    function downloadQrcode() {
      const a = downloadQrcodeRef.value
      if (a) {
        a.click()
      }
    }

    function copyAddress() {
      copyToClipboard(address.value)
      Toast("复制成功")
    }

    return {
      state,
      disabled,
      loading,
      address,
      downloadQrcodeRef,
      downloadQrcode,
      copyAddress,
      customerServicePopupShow,
      numFormatter,
      imageUrls,
      afterRead,
      onSubmit,
    }
  },
})
