<template>
  <div class="asset-detail-tab">
    <div :class="['asset-detail-tab__card', `asset-detail-tab__card--${type}`]">
      <div class="asset-detail-tab__card__cny-title">
        <div class="asset-detail-tab__card__cny-title__name">账户余额</div>
        <div class="asset-detail-tab__card__cny-title__icon" @click.stop="$emit('eye', !eye)">
          <van-icon v-if="eye" name="eye-o" size="14" />
          <van-icon v-else name="closed-eye" size="14" />
        </div>
      </div>
      <div class="asset-detail-tab__card__cny-value">{{ balanceWithClosed }}</div>
      <div class="asset-detail-tab__earnings">
        <div class="asset-detail-tab__earnings__yesterday">
          <div class="asset-detail-tab__earnings__name">昨日收入</div>
          <div class="asset-detail-tab__earnings__value">{{ yIncomeWithClosed }}</div>
        </div>
        <div class="asset-detail-tab__earnings__total">
          <div class="asset-detail-tab__earnings__name">累计收入</div>
          <div class="asset-detail-tab__earnings__value">{{ allIncomeWithClosed }}</div>
        </div>
      </div>
    </div>
    <van-row gutter="40" class="asset-detail-tab__balance-l1" v-if="type !== 'usdt'">
      <!--      <van-col span="12">
              <asset-detail-balance name="ETH可用余额" amount="228.2342" />
            </van-col>-->
      <van-col span="12">
        <asset-detail-balance :name="`${typeUpper}产品租金`" :amount="rent" />
      </van-col>
      <van-col class="van-hairline--left" span="12">
        <asset-detail-earning name="合约产品收入" amount="228.2342" :added-amount="pIncome" />
      </van-col>
    </van-row>
