  </div>
</template>

<script lang="ts">
import { computed, defineComponent } from "vue"
import { Col, Icon, Row, Tab, Tabs } from "vant"
import AssetDetailBalance from "@/views/asset/AssetDetailBalance.vue"
import AssetDetailEarning from "@/views/asset/AssetDetailEarning.vue"
import _ from "lodash"

export default defineComponent({
  name: "AssetDetailTab",
  components: {
    AssetDetailEarning,
    AssetDetailBalance,
    [Icon.name]: Icon,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Row.name]: Row,
    [Col.name]: Col,
  },
  props: {
    type: String,
    eye: {
      type: Boolean,
      default: true,
    },
    balance: {
      type: [String, Number],
      default: 0,
    },
    yIncome: {
      type: [String, Number],
      default: 0,
    },
    allIncome: {
      type: [String, Number],
      default: 0,
    },
    pIncome: {
      type: [String, Number],
      default: 0,
    },
    rent: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const typeUpper = computed(() => _.toUpper(props.type))
    const balanceWithClosed = computed(() => (props.eye ? props.balance : "****"))
    const yIncomeWithClosed = computed(() => (props.eye ? props.yIncome : "****"))
    const allIncomeWithClosed = computed(() => (props.eye ? props.allIncome : "****"))
    return {
      typeUpper,
      balanceWithClosed,
      yIncomeWithClosed,
      allIncomeWithClosed,
    }
  },
})
</script>
