<template>
  <div class="asset-detail-tabs">
    <van-tabs
      v-model:active="active"
      background="#0e72e2"
      color="#fff"
      title-active-color="#fff"
      title-inactive-color="#ccc8c6"
      line-width="30px"
      animated
    >
      <van-tab name="eth" title="ETH资产">
        <asset-detail-tab
          type="eth"
          :balance="wallet?.eth?.balance"
          :yIncome="wallet?.eth?.yIncome"
          :allIncome="wallet?.eth?.allIncome"
          :pIncome="wallet?.eth?.pIncome"
          :rent="wallet?.eth?.rent"
          :eye="eye"
          @eye="onEye"
        />
      </van-tab>
      <van-tab name="btc" title="BTC资产">
        <asset-detail-tab
          type="btc"
          :balance="wallet?.btc?.balance"
          :yIncome="wallet?.btc?.yIncome"
          :allIncome="wallet?.btc?.allIncome"
          :pIncome="wallet?.btc?.pIncome"
          :rent="wallet?.btc?.rent"
          :eye="eye"
          @eye="onEye"
        />
      </van-tab>
      <van-tab name="usdt" title="USDT资产">
        <asset-detail-tab
          type="usdt"
          :balance="wallet?.usdt?.balance"
          :yIncome="wallet?.usdt?.yIncome"
          :allIncome="wallet?.usdt?.allIncome"
          :pIncome="wallet?.usdt?.pIncome"
          :rent="wallet?.usdt?.rent"
          :eye="eye"
          @eye="onEye"
        />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, watchEffect } from "vue"
import { Tab, Tabs, Toast } from "vant"
import AssetDetailTab from "@/views/asset/AssetDetailTab.vue"
import WalletService, { Wallet } from "@/services/wallet"

export default defineComponent({
  name: "AssetDetailTabs",
  components: {
    AssetDetailTab,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
  },
  setup(props, { emit }) {
    const active = ref("btc")
    const eye = ref(true)
    const wallet = ref<Wallet>({})

    watchEffect(() => {
      emit("changeType", active.value)
    })

    function refreshWallet() {
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      WalletService.getWallet()
        .then(({ data }) => {
          wallet.value = data
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    onMounted(refreshWallet)

    function onEye(e: boolean) {
      eye.value = e
    }

    return {
      active,
      eye,
      onEye,
      wallet,
    }
  },
})
</script>
<style lang="scss">
@import "../../styles/prefix";
@import "../../styles/variables";

.asset-detail-tabs {
  .van-tabs__wrap {
    padding-bottom: $padding-lg;
  }

  .van-tab__text {
    font-size: $font-size-lg;
    font-weight: bold;
  }
}
</style>
