<template>
  <div class="bill-filter-bar">
    <van-dropdown-menu class="bill-filter-bar" active-color="#0e72e2">
      <van-dropdown-item
        ref="item1Ref"
        v-model="state.value1"
        :options="option1"
        @change="coinChange"
      />
      <van-dropdown-item title-class="bill-filter-bar__date" :options="option3" disabled>
        <template #title>
          <div class="bill-filter-bar__date__title" @click="onDateClick">
            <span class="bill-filter-bar__date__text">时间</span>
            <van-icon :name="timeSort" size="12" />
          </div>
        </template>
      </van-dropdown-item>
      <van-dropdown-item
        ref="item3Ref"
        v-model="state.value2"
        :options="option2"
        @change="typeChange"
      />
    </van-dropdown-menu>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from "vue"
import { Divider, DropdownItem, DropdownMenu, Icon } from "vant"

export default defineComponent({
  name: "BillFilterBar",
  components: {
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [Icon.name]: Icon,
    [Divider.name]: Divider,
  },
  setup(props, context) {
    const item1Ref = ref()
    const item3Ref = ref()
    const timeSort = ref("arrow-down")
    const state = reactive({
      value1: "全部币种",
      value2: "全部类型",
      timeValue: "time",
    })
    const option1 = [
      { text: "全部币种", value: "全部币种" },
      { text: "ETH", value: "eth" },
      { text: "BTC", value: "btc" },
      { text: "USDT", value: "usdt" },
    ]
    const option2 = [
      { text: "全部类型", value: "全部类型" },
      { text: "转入", value: "recharge" },
      { text: "后台转入", value: "system" },
      { text: "转出", value: "withdraw" },
    ]
    const option3: never[] = []

    const timeOption = [{ text: "时间", value: "time" }]

    function onDateClick() {
      item1Ref.value?.toggle(false)
      item3Ref.value?.toggle(false)
      // 改变时间图标样式
      timeSort.value = timeSort.value === "arrow-down" ? "arrow-up" : "arrow-down"
      context.emit("timeChangeRecapture", timeSort.value)
    }

    // 币种改变
    function coinChange(value: string): void {
      context.emit("coinChangeRecapture", value)
    }

    // 类型改变
    function typeChange(value: string): void {
      context.emit("typeChangeRecapture", value)
    }

    return {
      item1Ref,
      item3Ref,
      onDateClick,
      state,
      option1,
      option2,
      option3,
      timeOption,
      timeSort,
      coinChange,
      typeChange,
    }
  },
})
</script>
<style lang="scss">
@import "../../styles/prefix";
@import "../../styles/variables";

.bill-filter-bar {
  &__date {
    .van-ellipsis {
      display: flex;
      align-items: center;
    }

    &__text {
      color: #323233;
      padding-right: $padding-xs;
    }

    .van-icon {
      padding-right: $padding-xs;
    }

    &::after {
      display: none;
    }
  }
}
</style>
