
import { computed, defineComponent, ref } from "vue"
import { Cell, CellGroup, Divider, Loading, Toast } from "vant"
import TransactionService from "@/services/transaction"

export default defineComponent({
  name: "BillListItem",
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Divider.name]: Divider,
    [Loading.name]: Loading,
  },
  props: {
    type: String,
    amount: String,
    createTime: String,
    order_no: String,
    comment: String,
    status: String,
    currency: String,
  },
  setup(props, { emit }) {
    // 后台定义好接口之后需要根据 type 增加 + 或者 -
    const coinCount = computed(() => props.amount + " " + props.currency?.toUpperCase())

    const loading = ref(false)
    const showCancel = ref(true)

    function onCancelWithdraw() {
      if (loading.value) {
        return
      }
      loading.value = true
      TransactionService.cancelWithdraw(props.order_no)
        .then(({ status, message }) => {
          if (status !== 200) {
            Toast(message)
            return
          }
          showCancel.value = false
          emit("cancel")
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      coinCount,
      loading,
      showCancel,
      onCancelWithdraw,
    }
  },
})
