<template>
  <asset-transaction-header :type="modelValue" :title="titleValue" />
  <div class="asset-transaction-switch" @click="onOpen">
    <div class="asset-transaction-switch__text">切换</div>
    <div class="asset-transaction-switch__icon">
      <van-icon name="arrow" />
    </div>
  </div>
  <van-popup
    class="asset-transaction-switch__popup"
    v-model:show="popupShow"
    round
    position="bottom"
  >
    <van-cell title="ETH" label="余额: 1111" clickable @click="onSwitch('eth')">
      <template #icon>
        <BitcoinLogo name="eth" size="26" />
      </template>
    </van-cell>
    <van-cell title="BTC" label="余额: 1111" clickable @click="onSwitch('btc')">
      <template #icon>
        <BitcoinLogo name="btc" size="26" />
      </template>
    </van-cell>
    <van-cell title="TRON/USDT" label="余额: 1111" clickable @click="onSwitch('usdt')">
      <template #icon>
        <BitcoinLogo name="usdt" size="26" />
      </template>
    </van-cell>
  </van-popup>
</template>

<script lang="ts">
import { Cell, Icon, Popup } from "vant"
import { computed, defineComponent, ref } from "vue"
import AssetTransactionHeader from "./AssetTransactionHeader.vue"
import BitcoinLogo from "@/components/asset/BitcoinLogo.vue"

export default defineComponent({
  name: "AssetTransactionSwitch",
  components: {
    BitcoinLogo,
    AssetTransactionHeader,
    [Popup.name]: Popup,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
  },
  props: {
    modelValue: {
      type: String,
      default: "btc",
    },
    title: String,
  },
  setup(props, { emit }) {
    const popupShow = ref(false)

    function onOpen() {
      popupShow.value = !popupShow.value
    }

    function onSwitch(type: string) {
      popupShow.value = false
      emit("update:modelValue", type)
    }

    const titleValue = computed(() => {
      return props.title + props.modelValue.toUpperCase()
    })

    return {
      popupShow,
      titleValue,
      onOpen,
      onSwitch,
    }
  },
})
</script>
