<template>
  <van-image :width="size" :height="size" :style="style" round :src="src" />
</template>

<script lang="ts">
import { computed, CSSProperties, defineComponent } from "vue"
import { Image } from "vant"

export default defineComponent({
  name: "BitcoinLogo",
  components: {
    [Image.name]: Image,
  },
  props: {
    name: {
      type: String,
      default: "btc",
    },
    size: {
      type: [String, Number],
      default: "32px",
    },
  },
  setup(props) {
    const src = computed(() => require(`../../assets/coins/${props.name}.png`))
    const style = computed(() => {
      const styleIf: CSSProperties = {}
      if (props.name === "btc") {
        styleIf.background = "#e6be29"
      } else if (props.name === "eth") {
        styleIf.background = "#0d77db"
      } else if (props.name === "usdt") {
        styleIf.background = "#42c551"
      }

      styleIf.padding = "8px"
      return styleIf
    })

    return {
      src,
      style,
    }
  },
})
</script>
